@import "project.less";
@import "smart-grid.less";
.header-content{
    p{
        width: 100%;
    }
    div{
        display: flex;
        button{
            margin:5px;
        }

    }
}
.easy_useful {
    .item{
        p{
            width:100%;
        }
    }
}
.counts .items{

    .item{
      min-height: 150px;
    display: block;
    position: relative;
    img{margin:50px 10px;}
    span{
        position: absolute;
       top: 62px;
       left: 50%;
       margin-left: -27px;
    }
    }
}
.testimonials{
    button{
        max-width: 135px;
        display: block;
        .md-block({
            position: relative;
            left:50%;
            margin-left:-67px;
            });

    }

    .item:last-child{
        
        div{
            img{
            @media (max-width:1190px) {
            display: none;
            }
            }
        }
    }



}

