.header-content p {
  width: 100%;
}

.header-content div {
  display: flex;
}

.header-content div button {
  margin: 5px;
}

.easy_useful .item p {
  width: 100%;
}

.counts .items .item {
  min-height: 150px;
  display: block;
  position: relative;
}

.counts .items .item img {
  margin: 50px 10px;
}

.counts .items .item span {
  position: absolute;
  top: 62px;
  left: 50%;
  margin-left: -27px;
}

.testimonials button {
  max-width: 135px;
  display: block;
}

@media (max-width: 1190px) {
  .testimonials .item:last-child div img {
    display: none;
  }
}

@media screen and (max-width: 992px) {
  .testimonials button {
    position: relative;
    left: 50%;
    margin-left: -67px;
  }
}